body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    width: 100%;
    text-align: center;
}

.header h1 {
    color: #2196f3;
}
.body {
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.button-container, .form-container {
    margin: auto;
    width: 50%;
    display: flex;
    flex-direction: column;
}

.home .button-container {
    margin-top: 100px;
}

.town-hall {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 5px;
    margin-top: 20px;
}

.centered-content {
    display: flex;
    justify-content: center;
}

p {
    margin-left: 10px;
    margin-right: 10px;
}

.icon {
    width: 130px;
    margin: auto;
    display: block;
}

.role-assignment-title {
    text-align: center;
    font-size: 140%;
    margin-top: 0;
    margin-bottom: 30px;
}
